import React, { useRef, useMemo } from "react";
import { Marker, Popup } from "react-leaflet";
import { observer } from "mobx-react-lite";
import Store from "../../store/index.js";

const DraggableMarker = observer(() => {
  const markerRef = useRef(null);

  const eventHandlers = useMemo(() => ({
    dragend() {
      const marker = markerRef.current;
      if (marker != null) {
        Store.Location.draggableMarker(marker.getLatLng());
      }
    },
  }), []);

  return (
    <Marker
      draggable="true"
      eventHandlers={eventHandlers}
      position={ [Store.Location.coords.lat, Store.Location.coords.lng] }
      ref={ markerRef }
    >
      <Popup minWidth={90}>
        <h3>–£—Å—Ç–∞–Ω–æ–≤–∏—Ç–µ –º–∞—Ä–∫–µ—Ä –≤ –º–µ—Å—Ç–µ –ø—Ä–æ–≤–µ–¥–µ–Ω–∏—è –º–µ—Ä–æ–ø—Ä–∏—è—Ç–∏—è</h3>
      </Popup>
    </Marker>
  );
});

export default DraggableMarker;
