import React from "react";
import { observer } from "mobx-react";

const Input = observer(({ user }) => {
  const {
    email,
    password,
    userName,
    inputs,
    getInputValue,
    validateForm,
    showError,
  } = user;

  // eslint-disable-next-line array-callback-return
  return inputs.map((input, i) => {
    for (const key in input) {
      return (
        <React.Fragment key={`${i}1`}>
          <span
            className={`${input.type} hide error`}
            data-valid={`${input.type}`}
          >
            {input.type === "email"
              ? `Email —É–∫–∞–∑–∞–Ω –≤ –Ω–µ–≤–Ω—Ä–Ω–æ–º —Ñ–æ—Ä–º–∞—Ç–µ`
              : input.type === "password"
              ? `–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –æ—Ç 6 –¥–æ 12 —Å–∏–º–≤–æ–ª–æ–≤`
              : `Unknown Error !`}
          </span>
          <input
            onChange={(event) => {
              getInputValue(event.target.value, input.type);
            }}
            // eslint-disable-next-line no-loop-func
            onBlur={(event) => {
              validateForm(input.type, event.target.value, input.validations);
              showError(input.type);
            }}
            onFocus={() => (user.isDirty = false)}
            value={
              input.type === "email"
                ? email
                : input.type === "password"
                ? password
                : input.type === "text"
                ? userName
                : null
            }
            className={`reg-folder email-folder`}
            type={`${input.type}`}
            placeholder={`${input.placeholder}`}
          />
        </React.Fragment>
      );
    }
  });
});
export default Input;
