import React from 'react';
import styles from './_RatingModal.module.scss';
import closeIcon from '../../../modules/assets/close.svg';
import Leaves from '../Leaves/Leaves';
import { Link } from "react-router-dom";

export default function RatingModal({ setModalState, levels }) {

  function closeModal() {
    setModalState(false)
  }

  return (
    <div className={ styles.container } onClick={(elem) => {
      if(elem.target.classList.contains(styles.container)) {
        closeModal();
      }
    }}>
      <div className={ styles.main }>
        <div className={ styles.close_btn } onClick={ closeModal }>
          <img src={ closeIcon } alt="close-cross"/>
        </div>
        <div className={ styles.levels }>
          <p className={ styles.levels_title }>–£—Ä–æ–≤–Ω–∏</p>
          { levels.map((elem, id) => {
            return (
              <div className={ styles.level } key={ id }>
                <Leaves rating={ id + 1 } />
                <p className={ styles.level_title }>{elem}</p>
              </div>
            );
          }) }
        </div>
        <div className={ styles.description }>
          <p className={ styles.description_text }>
            –î–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –Ω–æ–≤–æ–≥–æ —É—Ä–æ–≤–Ω—è –Ω—É–∂–Ω—ã –æ—á–∫–∏ –∫–æ—Ç–æ—Ä—ã–µ –º–æ–∂–Ω–æ –ø–æ–ª—É—á–∏—Ç—å:
          </p>
          <div className={ styles.links }>
            <Link className={ styles.links_item + ' green_btn' } to='/initiatives'>–ò–Ω–∏—Ü–∏–∞—Ç–∏–≤–∞</Link>
            <Link className={ styles.links_item + ' green_btn' } to='/game'>–ò–≥—Ä–∞</Link>
          </div>
        </div>
      </div>
    </div>
  );
}